import React, {useState, Component} from 'react';
import Card from './CardsUI';
import img1 from '../../images/img1.jpg';
import img2 from '../../images/img2.jpg';
import img3 from '../../images/img3.jpg';
import Article from '../Articles/Article3';
import Article2 from '../Articles/Article2';
import Article3 from '../Articles/Article3';


function Cards() {
  
        const [show1, setShow1] = useState(false);
        const [show2, setShow2] = useState(false);
        const [show3, setShow3] = useState(false);


        return (  
            <>
            <div className='container-fluid d-flex justify-content-center' id='portfolio'>
                <div className='row'>
                    <div className='col-md-4'>
                       <Card btn={()=>setShow1(!show1)} imgsrc={img1} title='Lorem' des='Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat.'/> 
                    </div>
                    <div className='col-md-4'>
                        <Card btn={()=>setShow2(!show2)} imgsrc={img2} title='Ipsum' des='Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat.'/>
                    </div>
                    <div className='col-md-4'>
                        <Card btn={()=>setShow3(!show3)} imgsrc={img3} title='Transitioning Technological Society' des="Transhumanism is the concept that humanity can transcend through technological upgrades- to merge with machine. A belief to evolve beyond physicality and mental limitation as an artificial intelligence extension of yourself, with powers far greater than human standards. How will human life change according to this notion? Is transhumanism man's evolution or the becoming of sub-human?"/> 
                    </div>
                </div>

                
            </div>

        {
         show1?<Article btn={()=>setShow1(false)} />:null
        }

        {
         show2?<Article2 btn={()=>setShow2(false)} />:null
        }

        {
         show3?<Article3 btn={()=>setShow3(false)} />:null
        }

        
  </>
        );
    
}
 
export default Cards;