import React, { useState } from 'react';
import { FormControl } from 'react-bootstrap';
import './contact-form.css';
import {db} from '../../firebase';


const Contact = () => {

    const [name,setName] = useState("");
    const [email,setEmail] = useState("");
    const [subject,setSubject] = useState("");
    const [message,setMessage] = useState("");

/*
    const validateForm = () => {
    
        let myregex = new RegExp(/^[a-zA-Z0-9 _+-]*$/);
        let emailRegex = new RegExp(/^([a-zA-Z0-9_\-\.]+)@([a-zA-Z0-9_\-\.]+)\.([a-zA-Z]{2,5})$/);
        let message = document.getElementById('message').value;
        let email = document.getElementById('email').value;
        let name = document.getElementById('name').value;
        let subject = document.getElementById('subject').value;
    
    
        if(!myregex.test(name))
        {
            alert("Name not valid");
            return false;
        }
    
        if(!emailRegex.test(email))
        {
            alert("Email not valid");
            return false;
        }
    
        if(!myregex.test(subject))
        {
            alert("Subject not valid");
            return false;
        }
    
        if(!myregex.test(message))
        {
            alert("Message not valid");
            return false;
        }
        
        return true;
    }

*/
    const handleSubmit = (e) => {

            e.preventDefault();

            db.collection('contacts').add({
                name:name,
                email:email,
                subject:subject,
                message:message
            })
            .then(() => {
                alert('Message has been sent');
            })
     
            .catch((error) => {
                alert(error.message);
            });
     
            setName('');
            setEmail('');
            setSubject('');
            setMessage('');
         
        };
      

    return (
        <form id='contact' onSubmit={handleSubmit}>
            <h1>Get in Touch</h1>
            
            <div className='input-box'><FormControl placeholder='Name' value={name} onChange={(e) => setName(e.target.value)} required="required" id='message'/></div>
            <div className='input-box'><FormControl placeholder='Email' value={email} onChange={(e) => setEmail(e.target.value)} required="required" /></div>
            <div className='input-box'><FormControl placeholder='Subject' value={subject} onChange={(e) => setSubject(e.target.value)} required="required" /></div>
            <div className='input-box'><FormControl placeholder='Message' as='textarea' rows='5' value={message} onChange={(e) => setMessage(e.target.value)} required="required" /></div>
            <div className='sendBtn'><button type='submit' className='btn btn-outline-success'>Send Message</button></div>
        </form>
    )
}

export default Contact
