import { useRef, useEffect } from "react";
import './article.css';
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";

const Article3 = (props) => {

    gsap.registerPlugin(ScrollTrigger);
    const ref = useRef(null);

    useEffect(() => {
        const element = ref.current;
        gsap.from(element.querySelector(".line"), {
          scale: 0,
          ease: "none",
          scrollTrigger: {
            trigger: element.querySelector(".line"),
            scrub: true,
            start: "top bottom",
            end: "top top"
          }
        });
      }, []);

  return (
   
      <div id='article' ref={ref}>
      <h1>Transitioning Technological Society</h1>
      
      <p><span className='first-letter'>‘T</span>ranshumanism’ is the concept that humanity can transcend through technological upgrades- to merge with
machine. A belief to evolve beyond physicality and mental limitation as an artificial intelligence extension of
yourself, with powers far greater than human standards. How will human life change according to this notion? Is
transhumanism man's evolution or the becoming of sub-human? </p>

<p>The power of thought is arguably the most powerful thing on the planet; the brain encapsulates and is the main
source of creation. Humans have the capacity to change the environment and the entire ecosystem. As biological life
forms, we hold the key to our individual evolution, and we grasp our biological blueprint through our formalitycarrying the DNA of our ancestors. The physical body and mind work in harmony to enable us to experience the
natural world and communicate within it. Some teachings suggest that humanity can transcend through pure
awareness, which is everyone’s natural state of existence and the essence of our being. </p>

<p>However, as we transition to a technological society, there are circumstances to consider. Should energy of this
stature be uploaded and tethered to the cloud interface; imprisoning memories, feelings, thoughts and emotions to
quench the thirst for ‘digital immortality’. Once mind is uploaded digitally, would it be presumptuous to imply that
the physical being has lost all meaning – to be discarded and devalued to keep up with the machines. </p>

<p>Ethical concerns arise whether this transition impacts the social order of humanity. Are influential tech-companies
underrepresenting ethnic minorities? Well, it's important to understand that society has biases. As inventors develop
advanced technology, our biases infiltrate data collected by AI. Algorithms detect discrimination in the workforce
and in society as a whole. Along with the idea, others may reap the benefits of an upgrade, while another struggle to
make ends meet. Meanwhile, some become digitally immortal others are devalued by the system. </p>

<p>What happens in the cyber context impacts the real world and inversely. Humans develop a relationship with
machines that can either be beneficial or dysfunctional. Electronic devices can have neurological effects on the
brain, changing one's behaviour, personality and perceptions. Our complex biochemistry restructured through
influences like social media, video games, violent movies etc. It’s theorized that continuous exposure to violent
media desensitizes people. violence plagues the media, leading to devastating effects on the human psyche bringing
about increased real-life scenario of brutality. </p>

<p>Some studies show social media causes depression, anxiety and self-image issues- young people are susceptible to
mental health problems surrounding the censorship of the internet. Our minds are potentially vulnerable to distorted
perceptions of our environment. we start to develop addictive qualities, and infatuate with our alternative realities
unaware of the beauty, that is the natural world. According to Mary Aiken, on average a person will check their
phone 200 times a day, highlighting the compulsive behaviours spread in society.</p>

<p>Before we welcome technology with open arms, it's imperative to think of the various outcomes of seamlessly
bridging our very human existence with the machine. To become aware of the technological transition that is
happening at a rapid pace. Are we prepared for the irreversible change?</p>
      
      <div className='closeBtn'>
      <a href='#portfolio' className='btn btn-outline-danger' onClick={props.btn}>Close</a>
      </div>
       <p>
      <span className="line" />
       </p>
      </div>

  );
}

export default Article3;