import { useRef, useEffect } from "react";
import "./Home.css";
import banner from "../../images/banner.jpg";
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";

function Home() {

  gsap.registerPlugin(ScrollTrigger);
  const ref = useRef(null);
  
  
  useEffect(() => {
    const element = ref.current;
    gsap.from(
        element.querySelector(".main-image"),
        {
        scrollTrigger: {
            trigger: '.main-image',
            toggleActions: "restart reverse restart reverse",
            //             onEnter onLeave  onEnterBack  onLeaveBack
        },
        x:-400,
        duration: 3,
        opacity: 0,
        ease: 'none'
    });
  }, []);

  useEffect(() => {
    const element = ref.current;
    gsap.from(
        element.querySelector(".title-text"),
        {
        scrollTrigger: {
            trigger: '.title-text',
            toggleActions: "restart reverse restart reverse",
            //             onEnter onLeave  onEnterBack  onLeaveBack
        },
        x: 300,
        duration: 3,
        opacity: 0,
    });
  }, []);


  useEffect(() => {
    const element = ref.current;
    gsap.from(element.querySelector(".line"), {
      scale: 0,
      ease: "none",
      scrollTrigger: {
        trigger: element.querySelector(".line"),
        scrub: true,
        start: "top bottom",
        end: "top top"
      }
    });
  }, []);

  


  return (
    <div id='home' ref={ref}>


        <div className='container-fluid d-flex justify-content-center'>
                <div className='row'>
                    <div className='col-md-6 main-image'>
                    <img src={banner}  alt="main image" />
                    </div>

                    <div className='col-md-6 title-text'>
                    <p className='title'>Renea Boyd</p>
                    <p className='title2'>Content Writer</p>
                    <p className='about-text'>
                    Hello and welcome to the site where conversations are sparked about human behaviours, psychology and spirituality. Within this site, questions are asked frequently as to why ‘things’ are the way they are. Let’s drive into the deep ocean waters of complex topics and find the inner truths ourselves. Take a scroll with me on this adventure. 
                    </p>
                    </div>
                </div>
            </div>
        
        
      
        <p>
          <span className="line" />
        </p>

      </div>
        
  );
}
export default Home;